html, body {
  scroll-behavior: smooth;
  background: #fff;
  max-width: 100%;
  overflow-x: hidden;
  box-sizing: border-box;
}

.nav {
  background-color: transparent;
  overflow: hidden;
}

.nav a {
  float: left;
  display: block;
  color: #000;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 22px;
}

.nav a:not(:first-child){
  float: right;
}

.nav a img {
  height: 70px;
  width: 70px;
}

.nav a:hover {
  color: #5d5c61;
}

.nav .icon {
  display: none;
}

.landing-full-page {
  width: 100%;
  height: 100vh;
}

.landing-outer-container {
  height: 100vh;
  background-image: linear-gradient(to right, #fff 10%, #7395ae 10%);
}

#landing-subtitle {
  padding-top: 20vh;
}

.orientation {
  writing-mode: vertical-rl;
  text-orientation: sideways;
}

#landing-arrow-wrapper {
  position: absolute;
  bottom: 30px;
  right: 50%;
  padding: 20px;
}

.arrow {
  border: solid #000;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 9px;
}

.down {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}

.bounce {
  -moz-animation: bounce 2s infinite;
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0) rotate(45deg);
  }
  40% {
    transform: translateY(30px) rotate(45deg);
  }
  60% {
    transform: translateY(15px) rotate(45deg);
  }
}

#about{
  width: 100%;
  padding-top: 100px;
}

.text-link {
  text-decoration: none;
  color: #000;
}

.bold {
  font-weight: bold;
  font-size: 20px;
}

.heavy {
  font-weight: 900;
  font-size: 36px;
}

#skills {
  width: 100%;
  padding-top: 100px;
}

#projects{
  padding-top: 60px;
}

.spacing {
  margin-left: 20px;
}

.footer-container {
  width: 100%;
  text-align: center;
}

.footer-container li{
  text-decoration: none;
  list-style-type: none;
  display: inline;
}

.shake-3d {
  padding: 10px;
  -webkit-animation: shake3d 200ms 10;
  animation: shake3d 200ms 10;
  color: #000;
}

.shake-3d:hover {
  -webkit-animation: shake3d 200ms infinite;
  animation: shake3d 200ms infinite;
}

@keyframes shake3d {
  0% {
    text-shadow: 5px 4px red, -5px -6px blue;
  }
  25% {
    text-shadow: -5px -6px red, 5px 4px blue;
  }
  50% {
    text-shadow: 5px -4px red, -8px 4px blue;
  }
  75% {
    text-shadow: -8px -4px red, -5px -4px blue;
  }
  100% {
    text-shadow: -5px 0 red, 5px -4px blue;
  }
}

@media screen and (max-width: 600px) {
  .nav a:not(:first-child) {display: none;}
  .nav a.icon {
    float: right;
    display: block;
  }
}

@media screen and (max-width: 600px) {
  .nav.responsive {position: relative;}
  .nav.responsive a.icon {
    position: absolute;
    right: 0;
    top: 0;
  }
  .nav.responsive a {
    float: none;
    display: block;
    text-align: left;
  }
}